














import { Component, Vue } from 'vue-property-decorator';

@Component
export default class BaseCarousel extends Vue {
  flag = 0;
  length = 3;
  carouselName = 'carousel-next';
  toggleTimer = true;
  timerDelay = 3000;
  timer = 0;

  changeFlag(idx: number) {
    this.toggleTimer = false;
    clearInterval(this.timer);
    this.flag = idx;
  }

  toNext() {
    if (this.flag === (this.length - 1)) this.flag = 0;
    else this.flag += 1;
  }

  autoPlay() {
    this.timer = setInterval(() => {
      if (this.toggleTimer) this.toNext();
    }, this.timerDelay);
  }

  mounted() {
    // this.autoPlay();
  }

  beforeDestroy() {
    clearInterval(this.timer);
  }
}
